import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home-view.vue";
import AccountPage from "../views/AccountPage-view.vue";
import LogInPage from "../views/Login.vue";
import ConfirmedBooking from "../views/Confirmed-booking-view.vue";
import SignUpPage from "../views/signup-view.vue";
import BookingDetailsPage from "../views/Booking-details-view.vue";
import ChangeAppointmentPage from "../views/changeAppointment-view.vue";
import EditProfilePage from "../views/Edit_Profile-view.vue";
import EditProfileMemberPage from "../views/EditProfileMemberPage-view.vue";
import MemberIdPage from "../views/MemberID-view.vue";
import { useAuthStore } from "@/store/auth";

// import { auth } from "../firebase";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    // meta: {
    //   requiresAuth: true,
    // },
  },
  {
    path: "/AccountPage",
    name: "AccountPage",
    component: AccountPage,
  },
  {
    path: "/LogIn",
    name: "Log-InPage",
    component: LogInPage,
  },
  {
    path: "/ConfirmedBookingPage",
    name: "ConfirmedBookingPage",
    component: ConfirmedBooking,
  },
  {
    path: "/SignUpPage",
    name: "SignUpPage",
    component: SignUpPage,
  },
  {
    path: "/BookingDetailsPage",
    name: "BookingDetailsPage",
    component: BookingDetailsPage,
  },
  {
    path: "/ChangeAppointmentPage",
    name: "ChangeAppointmentPage",
    component: ChangeAppointmentPage,
  },
  {
    path: "/EditProfilePage",
    name: "EditProfilePage",
    component: EditProfilePage,
  },
  {
    path: "/EditProfileMemberPage",
    name: "EditProfileMemberPage",
    component: EditProfileMemberPage,
  },
  {
    path: "/MemberIdPage",
    name: "MemberIdPage",
    component: MemberIdPage,
  },

  

  

];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});



router.beforeEach(async (to) => {
  const publicPages = ['/LogIn','/SignUpPage']
  const authRequired = !publicPages.includes(to.path);

  const auth = useAuthStore();

  //if auth is required and the user is not logged in
  if(authRequired && !auth.user ){
    auth.returnUrl = to.fullPath;
    return '/LogIn';
  }

});

export default router;

